<template>
     <div ref="trys" style="height:100%">
         <div  class="all-container hasNoBackCon" >
          <div class="operate-div" ref="operateDiv">
             <datePicker ref="timer" type="doubleDate" :dateOpt="dateOpt"  @getDatePicker="getOption"></datePicker>
           </div>
          <div ref="myOwnTable" class="table-container" style="height:calc(100% - 4em)">
             <tables ref="myTable"  v-if="showTable" :tableOpt="TableOpt" style="margin:0 ;width: calc(100%);"></tables>
          </div>
        </div>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                searchParam: {
                  startDate: '',
                  endDate: ''
                },
                showTable: true,
                show: true,
                TableOpt: {
                    column: [
                      {title: '用户姓名', width: '120', field: 'customerName', textAlign: 'center'},
                      {title: '门牌编号', field: 'propertyName', textAlign: 'center',  width: '120'},
                      {title: '表号', width: '120', field: 'deviceNo', textAlign: 'center'},
                      {title: '表计户号', width: '140', field: 'meterUserNo', textAlign: 'center'},
                      {title: '充值金额', width: '140', field: 'payMoney', textAlign: 'center'},
                      {title: '充值日期', width: '150', field: 'payDate', textAlign: 'center'},
                      {title: '联系方式', width: '140', field: 'customerContact', textAlign: 'center'},
                      {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '180'}
                      // {title: '安装地址', field: 'installAddr', textAlign: 'center',  width: '140'},
                    ],
                    url: 'report/queryRechargeRecord',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20,40,60],
                    isExport: true,
                 //   noExportCurrent: true,
                    exportURL: 'report/queryRechargeRecordDown',
                    dataRow: 'list',
                    showTotal: true, 
                    queryParam: {},
                    totalOpt: [
                       {title: '充值金额', field: 'payMoney',  textAlign: 'center', width: '140'}
                    ]
                },
                searchDate:''
            }
        },
       computed: {
          dateOpt: function () {
            let date = this.initDate(true);
            return {
                      key: 'searchDate',
                      value: {startDate: date.start, endDate: date.end},
                      width: '25em',
                      fontSize: '0.8em'
                  }
          }
        },
        methods: {
           resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            getParams: function (pageNumber) {
              let arr = this.getSearchData(this.searchParam);
              this.searchTable(arr, pageNumber);
            }
        },
        created: function () {
          let that = this;
          let date = that.initDate(true);
          that.TableOpt.queryParam['startDate'] =  that.searchParam.startDate = date.start;
          that.TableOpt.queryParam['endDate'] =  that.searchParam.endDate =  date.end;
          that.showTable = true;
          // window.onresize = function () {
          //   that.getHeight();
          // }
        },
        watch: {
           searchDate: {
              handler(val, oldVal) {
                if (val !== '' && val !== oldVal) {
                  let arr = val.split('至');
                  this.searchParam.startDate = arr[0];
                  this.searchParam.endDate = arr[1];
                  this.getParams(1)
                }
              }
            }
        }
    }
</script>
<style lang="scss">

</style>



